import React from 'react'
import "./JumbotronStyle.css"

const Jumbotron = () => {
    return (
        <div className="jumbotron">
            <div className="over container body-content">
                <h1 className="display-5">A curated directory of books for you(shit kunt) to help you along your python programming peregrination</h1>
                <p className="lead">Buy them all mate ;)</p>
            </div>
            <div class="card" style="width: 18rem;">
  <div class="card-body">
    <h5 class="card-title">Card title</h5>
    <h6 class="card-subtitle mb-2 text-muted">Card subtitle</h6>
    <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
    <a href="#" class="card-link">Card link</a>
    <a href="#" class="card-link">Another link</a>
  </div>
</div>
        </div>
    )
}

export default Jumbotron